import React, {useEffect} from "react";
import $ from "jquery";
// Import Swiper React components
import { Swiper, SwiperSlide } from "swiper/react";

// Import Swiper styles
import "swiper/css";
import "swiper/css/effect-coverflow";
import "swiper/css/pagination";

import { Navigation } from "swiper";

export default function Projects() {
  useEffect(() => {
    $(".list").click(function () {
      const value = $(this).attr("data-filter");
      if (value == "all") {
        $(".item").show("500");
      } else {
        $(".item")
          .not("." + value)
          .hide("500");
        $(".item")
          .filter("." + value)
          .show("500");
      }
    });
    $(".list").click(function () {
      $(this).addClass("on").siblings().removeClass("on");
    });
  },[]);

  return (
    <>
      <section id="projects">
        <div className="inner">
          <ul>
            <li class="list on" data-filter="all">
              All
            </li>
            <li class="list" data-filter="clone">
              Clone
            </li>
            <li class="list" data-filter="redesign">
              ReDesign
            </li>
            <li class="list" data-filter="create">
              Create
            </li>
          </ul>

          {/* swiper clone*/}
          <div className="title">
            <h1>Clone</h1>
          </div>
          <Swiper
            slidesPerView={4}
            spaceBetween={20}
            slidesPerGroup={1}
            loop={true}
            loopFillGroupWithBlank={true}
            navigation={true}
            modules={[Navigation]}
            className="mySwiper1 item clone"
          >
            {/* 네이버와일 */}
            <SwiperSlide>
              <div className="item_img">
                <img
                  src={`${process.env.PUBLIC_URL}/images/results/naverwhale.jpg`}
                  alt="경로없음"
                />
                <button
                  onClick={() => window.open("https://url.kr/achf6x", "_blank")}
                >
                  Demo
                </button>
                <div className="item_bg"></div>
              </div>
              <div className="item_skill_tag">
                <ul>
                  <li>HTML5</li>
                  <li>CSS3</li>
                  <li>Javascript</li>
                  <li>React</li>
                  <li>Naver Whale</li>
                </ul>
              </div>
            </SwiperSlide>

            {/* 버거킹 */}
            <SwiperSlide>
              <div className="item_img">
                <img
                  src={`${process.env.PUBLIC_URL}/images/results/burgerking.png`}
                  alt="경로없음"
                />
                <button
                  onClick={() => window.open("https://url.kr/q74gic", "_blank")}
                >
                  Demo
                </button>
                <div className="item_bg"></div>
              </div>
              <div className="item_skill_tag">
                <ul>
                  <li>HTML5</li>
                  <li>CSS3</li>
                  <li>Javascript</li>
                  <li>Burger King</li>
                </ul>
              </div>
            </SwiperSlide>

            {/* 엠도씨 */}
            <SwiperSlide>
              <div className="item_img">
                <img
                  src={`${process.env.PUBLIC_URL}/images/results/mdoc.jpg`}
                  alt="경로없음"
                />
                <button
                  onClick={() => window.open("https://url.kr/byi9tk", "_blank")}
                >
                  Demo
                </button>
                <div className="item_bg"></div>
              </div>
              <div className="item_skill_tag">
                <ul>
                  <li>HTML5</li>
                  <li>CSS3</li>
                  <li>jQuery</li>
                  <li>반응형</li>
                  <li>M'c 남성화장품</li>
                </ul>
              </div>
            </SwiperSlide>

            {/* 페이팔 */}
            <SwiperSlide>
              <div className="item_img">
                <img
                  src={`${process.env.PUBLIC_URL}/images/results/paypal.jpg`}
                  alt="경로없음"
                />
                <button
                  onClick={() => window.open("https://url.kr/mwz586", "_blank")}
                >
                  Demo
                </button>
                <div className="item_bg"></div>
              </div>
              <div className="item_skill_tag">
                <ul>
                  <li>HTML5</li>
                  <li>CSS3</li>
                  <li>jQuery</li>
                  <li>반응형</li>
                  <li>paypal</li>
                </ul>
              </div>
            </SwiperSlide>

            {/* nature 풀빌라 */}
            <SwiperSlide>
              <div className="item_img">
                <img
                  src={`${process.env.PUBLIC_URL}/images/results/poolvila.jpg`}
                  alt="경로없음"
                />
                <div className="item_bg denied">제작중</div>
              </div>
              <div className="item_skill_tag">
                <ul>
                  <li>HTML5</li>
                  <li>CSS3</li>
                  <li>jQuery</li>
                  <li>반응형</li>
                  <li>paypal</li>
                </ul>
              </div>
            </SwiperSlide>
          </Swiper>

          {/* Re-Design */}
          <div className="title">
            <h1>Re-Design</h1>
          </div>
          <Swiper
            slidesPerView={4}
            spaceBetween={30}
            slidesPerGroup={1}
            // loop={true}
            // loopFillGroupWithBlank={true}
            navigation={true}
            modules={[Navigation]}
            className="mySwiper2 item redesign"
          >
            <SwiperSlide>
              <div className="item_img">
                <img
                  src={`${process.env.PUBLIC_URL}/images/results/samsung.jpg`}
                  alt="경로없음"
                />
                <button
                  onClick={() => window.open("https://url.kr/ay98dp", "_blank")}
                >
                  Demo
                </button>
                <div className="item_bg"></div>
              </div>
              <div className="item_skill_tag">
                <ul>
                  <li>HTML5</li>
                  <li>CSS3</li>
                  <li>jQuery</li>
                  <li>Samsung-Display-News</li>
                </ul>
              </div>
            </SwiperSlide>
          </Swiper>

          <div className="title">
            <h1>Create</h1>
          </div>
          <Swiper
            slidesPerView={4}
            spaceBetween={30}
            slidesPerGroup={1}
            // loop={true}
            // loopFillGroupWithBlank={true}
            navigation={true}
            modules={[Navigation]}
            className="mySwiper3 item create"
          >
            <SwiperSlide>
              <div className="item_img">
                <img
                  src={`${process.env.PUBLIC_URL}/images/results/fukuoka.jpg`}
                  alt="경로없음"
                />
                <button
                  onClick={() => window.open("https://url.kr/ay98dp", "_blank")}
                >
                  Demo
                </button>
                <div className="item_bg"></div>
              </div>
              <div className="item_skill_tag">
                <ul>
                  <li>HTML5</li>
                  <li>CSS3</li>
                  <li>JavaScript</li>
                  <li>반응형</li>
                  <li>Fukuoka-후쿠오카</li>
                </ul>
              </div>
            </SwiperSlide>
            <SwiperSlide>
              <div className="item_img">
                <img
                  src={`${process.env.PUBLIC_URL}/images/results/todolist.jpg`}
                  alt="경로없음"
                />
                <button
                  onClick={() => window.open("https://url.kr/tvfqbz", "_blank")}
                >
                  Demo
                </button>
                <div className="item_bg"></div>
              </div>
              <div className="item_skill_tag">
                <ul>
                  <li>HTML5</li>
                  <li>CSS3</li>
                  <li>ECMAScript 6</li>
                  <li>todolist - 오늘할일</li>
                </ul>
              </div>
            </SwiperSlide>
          </Swiper>
        </div>
      </section>
    </>
  );
}
