import React from "react";
import { BsGithub } from "react-icons/bs";
export default function Footer() {
  return (
    <>
      <footer id="footer">
        <span>&copy; 2022</span>
        <ul>
          <li
            onClick={() => window.open("https://github.com/pangkurr", "_blank")}>
            <BsGithub />
          </li>
        </ul>
        <p>
          Create by <span>JungHyo Yun</span>
        </p>
      </footer>
    </>
  );
}
