import React from "react";
import { AiOutlineMail, AiOutlineGithub } from "react-icons/ai";

export default function Contact() {
  
  return (
    <>
      <section id="contact">
        <div className="card_wrap">
          <div className="card">
            <div>
              <h1>Contact</h1>
              <ul>
                <li>
                  <h3><AiOutlineMail /></h3>
                   <b className="mail">hyo28456@naver.com</b>
                </li>
                <li onClick={()=>window.open('https://github.com/pangkurr','_blank')}>
                   <h3><AiOutlineGithub /></h3>
                   <b><u>https://github.com/pangkurr</u></b>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </section>
    </>
  );
}
